const InitialState = {
    dataKho: [],
    dataDanhMuc: [],
    dataBanHang: []
};
export const khoReducer = (state = InitialState, action) => {
    switch (action.type) {
        case 'CHANGE_KHO_DATA_KHO':
            return {
                ...state,
                dataKho: action.dataKho,
            };
        case 'CHANGE_KHO_DATA_DANHMUC':
            return {
                ...state,
                dataDanhMuc: action.dataDanhMuc,
            };
        case 'CHANGE_KHO_DATA_BANHANG':
            return {
                ...state,
                dataBanHang: action.dataBanHang,
            };
        default:
            return state;
    }

};