const initState = {
  openPageFull: false,
  openSideList: false,
  index: 1,
  namePage: '',
  timeServer: '00:00:00',
}
export const navReducer = (state = initState, action) => {
  switch (action.type) {
    case '_NAV_CHANGE_NAME_PAGE':
      return {
        ...state,
        namePage: action.namePage
      }
    case 'TOGGLE_FALSE_NAV_PAGE_FULL':
      console.log('navToggle ');
      return {
        ...state,
        openPageFull: false
      };
    case 'CHANGE_INDEX':
      console.log('CHANGE INDEX ');
      return {
        ...state,
        index: action.index
      };
    case 'TOGGLE_ON_OPEN_SIDELIST':
      return {
        ...state,
        openSideList: true
      };
    case 'TOGGLE_OFF_OPEN_SIDELIST':
      return {
        ...state,
        openSideList: false
      };
    case '_NAV_TIME_SERVER':
      console.log(action.time)
      return {
        ...state,
        timeServer: action.time
      }
    default:
      return state
  }
}
