import { firebaseConnection } from "../../../firebase";
import store from '../../../store';

var dataSnapshotDanhMuc = firebaseConnection.database().ref('Store/DanhMuc');
var dataSnapshotKho = firebaseConnection.database().ref('Store/Kho');
var dataSnapshotBanHang = firebaseConnection.database().ref('Store/BanHang');

export function GetDanhMuc(cb) {
    dataSnapshotDanhMuc.on('value', (danhMucs) => {
        let arrData = [];
        danhMucs.forEach(element => {
            arrData.push({
                key: element.key,
                maVach: element.val().maVach,
                tenSanPham: element.val().tenSanPham,
                quyCach1: element.val().quyCach1,
                donVi1: element.val().donVi1,
                quyCach2: element.val().quyCach2,
                donVi2: element.val().donVi2,
                quyCach3: element.val().quyCach3,
                donVi3: element.val().donVi3,
            });
        })
        cb(arrData);
    })
}
export function SetDanhMuc(element, cb) {
    let response = 0
    dataSnapshotDanhMuc.orderByChild('maVach').equalTo(element.maVach).once('value', (snapshot) => {
        if (snapshot.exists()) {
            console.log('trungid')
            response = 0
        } else {
            let temp = {
                maVach: element.maVach,
                tenSanPham: element.tenSanPham,
                quyCach1: element.quyCach1,
                donVi1: element.donVi1,
                quyCach2: element.quyCach2,
                donVi2: element.donVi2,
                quyCach3: element.quyCach3,
                donVi3: element.donVi3,
            }
            dataSnapshotDanhMuc.push(temp);
            response = 1
        }
    })
    cb(response)
}
export function UpdateDanhMuc(item) {
    if (item.key !== '') {
        dataSnapshotDanhMuc.child(item.key).child('quyCach1').set(item.quyCach1);
        dataSnapshotDanhMuc.child(item.key).child('donVi1').set(item.donVi1);
        dataSnapshotDanhMuc.child(item.key).child('quyCach2').set(item.quyCach2);
        dataSnapshotDanhMuc.child(item.key).child('donVi2').set(item.donVi2);
        dataSnapshotDanhMuc.child(item.key).child('quyCach3').set(item.quyCach3);
        dataSnapshotDanhMuc.child(item.key).child('donVi3').set(item.donVi3);
    }
}
export function RemoveDanhMuc(id) {
    dataSnapshotDanhMuc.child(id).remove();
}
export function ImportDataNhapDanhMuc(data) {
    data.forEach(element => {
        let temp = {
            maVach: element.maVach,
            tenSanPham: element.tenSanPham,
            quyCach1: element.quyCach1,
            donVi1: element.donVi1,
            quyCach2: element.quyCach2,
            donVi2: element.donVi2,
            quyCach3: element.quyCach3,
            donVi3: element.donVi3,
        }
        dataSnapshotDanhMuc.push(temp);
    })
}
