export function GetKho(cb) {

    dataSnapshotKho.on('value', (items) => {
        let arrData = [];
        items.forEach(element => {
            arrData.push({
                key: element.key,
                ctkm_sub: element.val().ctkm_sub,
                ctkm_percent: element.val().ctkm_percent,
                giaBan: element.val().giaBan,
                giaVon: element.val().giaVon,
                hanSuDung: element.val().hanSuDung,
                maVach: element.val().maVach,
                phiVanChuyen: element.val().phiVanChuyen,
                soLuong: element.val().soLuong,
                tenSanPham: element.val().tenSanPham,
                chietKhau: element.val().chietKhau,
                donGia: element.val().donGia,
                quyCach1: element.val().quyCach1,
                donVi1: element.val().donVi1,
                quyCach2: element.val().quyCach2,
                donVi2: element.val().donVi2,
                quyCach3: element.val().quyCach3,
                donVi3: element.val().donVi3,

            });
        })
        cb(arrData);
    })
}
export function setKho(element, cb) {
    let response = 0
    let temp = {
        maVach: element.maVach,
        tenSanPham: element.tenSanPham,
        hanSuDung: element.hanSuDung,
        donGia: element.donGia,
        soLuong: element.soLuong,
        chietKhau: element.chietKhau,
        phiVanChuyen: element.phiVanChuyen,
        giaVon: element.giaVon,
        giaBan: element.giaBan,
        ctkm_sub: element.ctkm_sub,
        ctkm_percent: element.ctkm_percent,
        quyCach1: element.quyCach1,
        donVi1: element.donVi1,
        quyCach2: element.quyCach2,
        donVi2: element.donVi2,
        quyCach3: element.quyCach3,
        donVi3: element.donVi3,
    }
    dataSnapshotKho.push(temp);
    response = 1
    cb(response)
}
export function RemoveKho(id, soLuong, cb) {
    if (soLuong == 0) {
        dataSnapshotKho.child(id).remove();
        cb(1)
    } else {
        cb(0)
    }
}
export function UpdateKho(item) {
    if (item.id !== '') {
        dataSnapshotKho.child(item.id).child('donGia').set(item.donGia);
        dataSnapshotKho.child(item.id).child('soLuong').set(item.soLuong);
        dataSnapshotKho.child(item.id).child('chietKhau').set(item.chietKhau);
        dataSnapshotKho.child(item.id).child('phiVanChuyen').set(item.phiVanChuyen);
        dataSnapshotKho.child(item.id).child('giaVon').set(item.giaVon);
        dataSnapshotKho.child(item.id).child('giaBan').set(item.giaBan);
        dataSnapshotKho.child(item.id).child('ctkm_sub').set(item.ctkm_sub);
        dataSnapshotKho.child(item.id).child('ctkm_percent').set(item.ctkm_percent);
    }
}

export function ImportDataNhapKho(data) {
    data.forEach(element => {
        dataSnapshotKho.orderByChild('maVach').equalTo(element.maVach).once('value', (snapshot) => {
            if (snapshot.exists()) {
                console.log('trungid')
                console.log(element.maVach)
            } else {
                let temp = {
                    maVach: element.maVach,
                    tenSanPham: element.tenSanPham,
                    hanSuDung: element.hanSuDung,
                    quyCach: element.quyCach,
                    donGia: element.donGia,
                    soLuong: element.soLuong,
                    chietKhau: element.chietKhau,
                    phiVanChuyen: element.phiVanChuyen,
                    giaVon: element.giaVon,
                    giaBan: element.giaBan,
                    ctkm_sub: element.ctkm_sub,
                    ctkm_percent: element.ctkm_percent,
                }
                dataSnapshotKho.push(temp);
            }
        })
    })
}

export function GetBanHang(cb) {
    dataSnapshotBanHang.on('value', (banHangs) => {
        let arrData = [];
        banHangs.forEach(element => {
            arrData.push({
                key: element.key,
                id: element.val().id,
                soLuong: element.val().soLuong,
                quyCach: element.val().quyCach,
                tenSanPham: element.val().tenSanPham,
                giaBanSauKM: element.val().giaBanSauKM,
            });
        })
        cb(arrData);
    })
}
export function SetBanHang(element) {
    let temp = {
        id: element.id,
        soLuong: element.soLuong,
        quyCach: element.quyCach,
        tenSanPham: element.tenSanPham,
        giaBanSauKM: element.giaBanSauKM,
    }
    dataSnapshotBanHang.push(temp);
}
export function RemoveBanHang(item) {
    dataSnapshotBanHang.child(item.key).remove();
}
export function RemoveBanHang_Update(item) {
    dataSnapshotBanHang.child(item.key).remove();
    dataSnapshotKho.once('value', (items) => {
        items.forEach(element => {
            if (element.key == item.id) {
                if (parseInt(element.val().soLuong) - parseInt(item.soLuong) >= 0) {
                    dataSnapshotKho.child(element.key).child('soLuong').set((parseInt(element.val().soLuong) - parseInt(item.soLuong)) >= 0 ? parseInt(element.val().soLuong) - parseInt(item.soLuong) : 0);
                }
            }
        })
    })
}