import React, { Component } from 'react';
import { HashRouter, Route, Switch, BrowserRouter } from 'react-router-dom';
import { connect } from 'react-redux';
// import { renderRoutes } from 'react-router-config';
import './App.scss';
import CustomizedSnackbars from './component/ScadaComponent/Snackbar/CustomizedSnackbars';

import 'devextreme/dist/css/dx.common.css';
import 'devextreme/dist/css/dx.material.blue.dark.css';
import * as actionKho from './redux/actions/khoAction'
import * as getData from './component/Screen/GetData/FirebaseGetData'
const loading = () => <div className="animated fadeIn pt-3 text-center">Loading...</div>;

// Containers
const DefaultLayout = React.lazy(() => import('./containers/DefaultLayout'));

// Pages
const Login = React.lazy(() => import('./views/Pages/Login'));
const Register = React.lazy(() => import('./views/Pages/Register'));

class App extends Component {
  componentWillMount(){
    getData.GetKho(Kho => {
      this.props.actionKhoChangeDataKho(Kho)
    })
    getData.GetDanhMuc(danhMuc => {
      this.props.actionKhoChangeDataDanhMuc(danhMuc)
    })
    getData.GetBanHang(banHang => {
      this.props.actionKhoChangeDataBanHang(banHang)
    })
  }
  render() {
    return (
      <HashRouter>
        {/* // basename={process.env.PUBLIC_URL}> */}
        <React.Suspense fallback={loading()}>
          <CustomizedSnackbars />
          <Switch>
            <Route exact path="/login" name="Login Page" render={props => <Login {...props} />} />
            <Route exact path="/register" name="Register Page" render={props => <Register {...props} />} />
            {/* <Route exact path="/404" name="Page 404" render={props => <Page404 {...props} />} /> */}
            {/* <Route exact path="/500" name="Page 500" render={props => <Page500 {...props} />} /> */}
            <Route path="/" name="Home" render={props => <DefaultLayout {...props} />} />
          </Switch>
        </React.Suspense>
      </HashRouter>
    );
  }
}

const mapStateToProps = (state, ownProps) => ({
  openPageFull: state.navPageFull.openPageFull,
  openSideList: state.navPageFull.openSideList,
})
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionKhoChangeDataKho: (item) => {
      dispatch(actionKho.actionKhoChangeDataKho(item))
    },
    actionKhoChangeDataDanhMuc: (item) => {
      dispatch(actionKho.actionKhoChangeDataDanhMuc(item))
    },
    actionKhoChangeDataBanHang: (item) => {
      dispatch(actionKho.actionKhoChangeDataBanHang(item))
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App)